import { Agent, AgentSetting } from "./agent.model";
import types from './types';
export interface AgentsState {
  agentsList: Agent[],
  agentsSettings: AgentSetting[]
}

const initialState: AgentsState = {
  agentsList: [],
  agentsSettings: []
}

export function agentsReducer(state = initialState, action) {
  switch (action.type) {
    case types.LOAD_AGENTS:
      return {
        ...state,
        agentsList: action.payload
      }
    case types.RESET_AGENTS_SETTINGS: 
      return {
        ...state,
        agentsSettings: action.payload
      }
    case types.SET_AGENT_POSITION:
      return {
        ...state,
        agentsSettings: state.agentsSettings.map(el => {
          if(el.name === action.payload.name && el.isOffense === action.payload.isOffense) {
            return action.payload;
          } else {
            return el;
          }
        }),
      }
    default: return state
  }
}